<template>
    <div>
        <el-row>
            <headers :title='title'></headers>
        </el-row>
        <el-row>
            <el-col :span='18' :offset='3'>
                <div class='modify_pw'>
                    <div class="modify_title">
                        <span>新增用户</span>
                        <span class="r">
                            <span class='rs'>*</span>
                            <span>必填项</span>
                        </span>
                    </div>
                    <el-row>
                        <el-col :span='10' :offset='7'>
                            <div class="ads as">
                                <el-form ref="form" :model="form" label-width="95px" :rules='rules'>
                                    <el-form-item label="登录账号：" class='is-required' prop='userName'>
                                        <el-input v-model="form.userName" placeholder='请填写登录账号' maxlength="20"></el-input>
                                    </el-form-item>
                                    <el-form-item label="登录密码：" class='is-required' prop='password'>
                                        <el-input type="password" v-model="form.password" placeholder='请填写登录密码' maxlength="20"></el-input>
                                    </el-form-item>
                                    <el-form-item label="员工姓名：" class='is-required' prop='name'>
                                        <el-input v-model="form.name" placeholder='请填写员工姓名' maxlength="20"></el-input>
                                    </el-form-item>
                                </el-form>
                            </div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span='6' :offset='9'>
                            <div class='subs'>
                                <el-row>
                                    <el-col :span='12' :offset='6'>
                                        <el-button type="primary" @click='sub("form")'>提交</el-button>
                                    </el-col>
                                    <!-- <el-col :span='12'>
										<el-button @click='reset' icon='el-icon-refresh'>重置</el-button>
									</el-col> -->
                                </el-row>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import headers from '../../components/header.vue'
export default {
    components: {
        headers
    },
    data() {
        return {
            title: '新建账号',
            form: {
                userName: '',
                password: '',
                name: ''
            },
            rules: {
                userName: [{
                    required: true,
                    message: '请输入账号',
                    trigger: 'blur'
                }, {
                    pattern: /^(\w){4,20}$/,
                    message: '只能输入4-20个字母、数字、下划线'
                }],
                password: [{
                    required: true,
                    message: '请输入密码',
                    trigger: 'blur'
                }, {
                    pattern: /^(\w){4,20}$/,
                    message: '只能输入4-20个字母、数字、下划线'
                }],
                name: [{
                    required: true,
                    message: '员工姓名不能为空'
                }]
            }
        }
    },
    methods: {
        /*    _this.$axios.post('/tomlive/user/insertSelectUser',this.$qs.stringify({
        	userName:_this.form.userName,
        	password:_this.form.password,
        	realName:_this.form.name
        })).then((res)=>{
        	if(res.data.code==201){
        		alert(res.data.message)
        	}else{
        		console.log(res)
        		this.$router.push('/home/account_add_s')
        	}
        })*/
        /*重置*/
        reset() {
            this.form.userName = '',
                this.form.password = '',
                this.form.name = ''
        },
        /*提交*/
        sub(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // alert('submit!');
                    var _this = this
                    this.$confirm('是否确认提交?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then((action) => {
                        console.log(action)
                        if (action === 'confirm') {
                            _this.$axios.post('/tomlive/user/insertSelectUser', this.$qs.stringify({
                                userName: _this.form.userName,
                                password: _this.form.password,
                                realName: _this.form.name
                            })).then((res) => {
                                if (res.data.code == 201) {
                                    alert(res.data.message)
                                } else {
                                    console.log(res)
                                    this.$router.push('/home/account_manage')
                                }
                            });
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '添加失败'
                        });
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            })
            /*var _this = this
            this.$confirm('是否确认提交?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then((action) => {
                console.log(action)
                if (action === 'confirm') {
                    _this.$axios.post('/tomlive/user/insertSelectUser', this.$qs.stringify({
                        userName: _this.form.userName,
                        password: _this.form.password,
                        realName: _this.form.name
                    })).then((res) => {
                        if (res.data.code == 201) {
                            alert(res.data.message)
                        } else {
                            console.log(res)
                            this.$router.push('/home/account_add_s')
                        }
                    });
                }
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '添加失败'
                });
            })*/
        }
    }
}
</script>
<style scoped>
.modify_pw {
    width: 100%;
    min-height: 800px;
    margin: 20px 0 0 0;
    background: #fff;
    border: 1px solid #e9e9e9;
    box-sizing: border-box;
}

.modify_title {
    width: 100%;
    height: 50px;
    border-bottom: 1px solid #e9e9e9;
    font-size: 14px;
    line-height: 50px;
    color: #666;
    font-weight: bold;
    box-sizing: border-box;
}

.modify_title>span {
    margin: 0 20px 0 20px;
}

.ads {
    width: 100%;
    height: 40px;
    margin: 20px 0 0 0;
}

.as {
    margin: 90px 0 0 0;
}

.subs {
    width: 100%;
    height: 40px;
    margin: 178px 0 0 0;
}

.subs>.el-row>.el-col>button {
    padding: 12px 30px;
}

.r {
    float: right;
}

.r>.rs {
    color: red;
    margin: 0px 5px;
}
</style>
